import React, { Component, Fragment } from "react";
import Highcharts, { Point } from "highcharts";
import HighchartsReact from "highcharts-react-official";

class StackedBar extends Component {
  state = {
    parent: this.props.parent,
    child: this.props.child,
  };

  render() {
    //App.js   
    const options = {
      chart: {
        type: "bar",
        width: 1000,
        height: 200, // bar.    line chart .
      },
      title: {
        text: "",
        style: {
          fontWeight: "bold",
        },
      },
      credits: {
        enabled: false,
      },
      xAxis: {
        categories: [""],
        labels: "none",
      },

      yAxis: {
        title: null,
        tickInterval: 20,
        min: 0,
        max: 100,
      },

      legend: {
        enabled: false,
      },
      tooltip: {
        enabled: false,
      },
      plotOptions: {
        series: {
          stacking: "percent",
          dataLabels: [
            {
              enabled: true,
            },
          ],
        },
      },
      series: [
        {
          dataLabels: [
            {
              format: "",
              style: {
                fontSize: "14px",
                color: "#FFFFFF",
                textDecoration: "none",
              },
            },
          ],
          data: [this.state.child],
          color: "#4863A0",
          name: "",
        },
        {
          dataLabels: [
            {
              format: "",
              style: { fontSize: "14px", color: "#FFFFFF" },
            },
          ],
          data: [this.state.parent],
          color: "#F9BE00",
          name: "",
        },
      ],
    };
    return (
      <Fragment>
        <div style={{ padding: "30px 0" }}>
          <HighchartsReact highcharts={Highcharts} options={options} />
        </div>
      </Fragment>
    );
  }
}
export default StackedBar;
