import React from "react";
import { Grid } from "@material-ui/core";

export default function Personal(props) {
  const name = props.name;
  const gender = props.gender;
  const dateOfBirth = props.dateOfBirth;
  const broSis = props.broSis;
  const impaired = props.impaired;
  const impairment = props.impairment;
  const primaryLanguage = props.primaryLanguage;
  const schoolLanguage = props.schoolLanguage;
  const problem = props.problem;
  const reason = props.reason;
  const improvement = props.improvement;
  const awareness = props.awareness;

  const spanStyle = {
    fontSize: "18px",
    fontWeight: "bold",
    padding: "0px 30px",
  };

  return (
    <div
      style={{
        fontFamily: "'Noto Sans KR', sans serif;",
        fontSize: "18px",
        marginTop: "20px",
        marginBottom: "50px",
      }}
    >
      <div>
        <p>
          <span style={spanStyle}>  : </span>
          {name}
        </p>
        <p>
          <span style={spanStyle}> :</span> {gender}
        </p>
        <p>
          <span style={spanStyle}> :</span> {dateOfBirth}
        </p>
        <p>
          <span style={spanStyle}>:</span>{" "}
          {broSis === "Yes" ? "" : ""}
        </p>
        <p>
          <span style={spanStyle}> : </span>
          {impaired === "Yes" ? { impairment } : ""}
        </p>
        <p>
          <span style={spanStyle}>  :</span> {primaryLanguage}
        </p>
        <p>
          <span style={spanStyle}>  :</span>{" "}
          {schoolLanguage}
        </p>
        <p>
          <span style={spanStyle}>  :</span> {problem}
        </p>
        <p>
          <span style={spanStyle}> : </span>
          {reason}
        </p>
        <p>
          <span style={spanStyle}> :</span>
          {improvement === "getting better"
            ? " "
            : improvement === "getting worse"
            ? " "
            : " "}
        </p>
        <p>
          <span style={spanStyle}>   :</span>

          {awareness}
        </p>
      </div>
    </div>
  );
}
